<template>
  <div class="aside">
    <el-menu
      :default-active="$route.path"
      class="el-menu-vertical-demo"
      background-color="#23262E"
      text-color="#fff"
      :collapse="isCollapse"
      unique-opened
      router
    >
    <el-button-group class="left-button">
      <el-button
        size="mini"
        type="primary"
        :icon="isCollapse==true?'el-icon-s-fold':'el-icon-s-unfold'"
        @click="isCollapse = !isCollapse"
      ></el-button>
    </el-button-group>
     
      <!-- 一级菜单 -->
      <template v-for="(p, i) of data">
        <!-- 不带二级 -->
        <el-menu-item :key="i" :index="p.ChannelUrl" v-if="p.HasChild == 0 && p.ChannelDepth == 1">
          <i class="el-icon-menu"></i>
          <span slot="title">{{ p.ChannelName }}</span>
        </el-menu-item>
        <!-- 带二级 -->
        <el-submenu :key="i" :index="p.ChannelUrl" v-if="p.HasChild == 1 && p.ChannelDepth == 1">
          <template slot="title">
            <i class="el-icon-menu"></i>
            <span slot="title">{{ p.ChannelName }}</span>
          </template>
          <!-- 二级菜单 -->
          <template v-for="(item, index) of data">
            <!-- 不带三级 -->
            <el-menu-item :key="index" :index="item.ChannelUrl" v-if=" item.Parent == p.ChannelID && item. ChannelDepth == 2" >
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span slot="title">{{ item.ChannelName }}</span>
              </template>
            </el-menu-item >
            <!-- 带三级 -->
            <!-- <el-submenu :key="index" :index="item.ChannelUrl" v-if=" item.Parent == p.ChannelID && item. ChannelDepth == 2 && item.HasChild == 1 " >
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span slot="title">{{ item.ChannelName }}</span>
              </template>
              <template v-for="(items, indexs) of data"> -->
                <!-- 三级菜单 -->
                <!-- <el-menu-item :key="indexs" v-if=" items.Parent == item.ChannelID && items.ChannelDepth == 3 " :index="items.ChannelUrl" >
                  <template slot="title">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{ items.ChannelName }}</span>
                  </template>
                </el-menu-item >
              </template>
            </el-submenu> -->
          </template>
        </el-submenu>
      </template>
      <el-menu-item index="/age">
          <i class="el-icon-menu"></i>
          <span slot="title">模板消息</span>
        </el-menu-item>
    </el-menu>
  </div>
</template> 

<script>
export default {
  data() {
    return {
      isCollapse: false,
      data: [],
    };
  },
  created() {
    // this.get();
  },
  methods: {
    get() {
      this.$axios
        .get("/index.php/Api/GetChannel", {
          params: {
            Field: "ChannelID",
            Depth: -1,
          },
        })
        .then((res) => {
          this.data = res.data.Data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
<style lang="scss" scope>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
.aside{
  height: 100%;
}
.el-menu{
  border: none;
}
.el-menu-item:focus{
  background: none;
}
.el-menu-item:hover>span,
.el-submenu__title:hover>span{
  color: #409EFF;
}
.left-button{
  display: flex;
  justify-content: right;
  padding-right: 10px;
}
</style>